import React, { Component, Fragment } from 'react';
import { Table, Select } from 'antd';

const { Option } = Select;
const dataSource = [
  {
    lop: 'SCOT1',
    mon: 'Anh văn 1',
    sohoctrinh: '6',
    lopghep: '',
    loaigio: '1',
    topthu: '0',
    stiecctdt: '60',
    stiectuan: '6',
    stieckh: '60',
    stiectkb: '12',
    ca: '0',
    giaidoan: '1',
    tutuan: '1',
    dentuan: '10',
    tungay: '09/08/2019',
    denngay: '14/10/2020',
  },
  {
    lop: 'SCOT2',
    mon: 'Giải tích 1',
    sohoctrinh: '2',
    lopghep: '',
    loaigio: '1',
    topthu: '0',
    stiecctdt: '30',
    stiectuan: '4',
    stieckh: '30',
    stiectkb: '8',
    ca: '0',
    giaidoan: '1',
    tutuan: '1',
    dentuan: '10',
    tungay: '09/08/2019',
    denngay: '14/10/2020',
  },
];

const columns = [
  {
    title: 'Tên lớp',
    dataIndex: 'lop',
    key: 'lop',
  },
  {
    title: 'Tên môn',
    dataIndex: 'mon',
    key: 'mon',
  },
  {
    title: 'Số học trình',
    dataIndex: 'sohoctrinh',
    key: 'sohoctrinh',
  },
  {
    title: 'Lớp ghép',
    dataIndex: 'lopghep',
    key: 'lopghep',
  },
  {
    title: 'Loại giờ',
    dataIndex: 'loaigio',
    key: 'loaigio',
  },
  {
    title: 'Tốp thứ',
    dataIndex: 'topthu',
    key: 'Tốp thứ',
  },
  {
    title: 'S.tiết CTĐT',
    dataIndex: 'stiecctdt',
    key: 'stietctdt',
  },
  {
    title: 'S.tiết/tuần',
    dataIndex: 'stiectuan',
    key: 'stiettuan',
  },
  {
    title: 'S.tiết xếp KH',
    dataIndex: 'stieckh',
    key: 'stietkh',
  },
  {
    title: 'S.tiết xếp TKB',
    dataIndex: 'stiectkb',
    key: 'stiettkb',
  },
  {
    title: 'Ca',
    dataIndex: 'ca',
    key: 'ca',
  },
  {
    title: 'Giai đoạn',
    dataIndex: 'giaidoan',
    key: 'giaidoan',
  },
  {
    title: 'Từ tuần',
    dataIndex: 'tutuan',
    key: 'tutuan',
  },
  {
    title: 'Đến tuần',
    dataIndex: 'dentuan',
    key: 'dentuan',
  },
  {
    title: 'Từ ngày',
    dataIndex: 'tungay',
    key: 'tungay',
  },
  {
    title: 'Đến ngày',
    dataIndex: 'denngay',
    key: 'denngay',
  },
  {
    title: 'Giáo viên',
    key: 'giaovien',
    dataIndex: 'gvcn',
    render: () => (
      <Fragment>
        <Select defaultValue="lucy" style={{ width: '100%' }}>
          <Option value="jack">Trần Văn A</Option>
          <Option value="lucy">Nguyễn Văn B</Option>
          <Option value="Yiminghe">Trần Văn C</Option>
        </Select>
      </Fragment>
    ),
  },
  {
    title: 'Phòng',
    dataIndex: 'phong',
    key: 'phong',
    render: () => (
      <Fragment>
        <Select defaultValue="1" style={{ width: '100%' }}>
          <Option value="1">Phòng 101</Option>
          <Option value="2">Phòng 102</Option>
        </Select>
      </Fragment>
    ),
  },
  {
    title: 'Ghi chú',
    dataIndex: 'ghichu',
    key: 'ghichu',
  },
];

export default class TableClasses extends Component {
  state = {};
  render() {
    return (
      <Table scroll={{ x: true }} bordered size="small" dataSource={dataSource} columns={columns} />
    );
  }
}



